.preloader {
    background: #ecf0f1;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .preloader svg {
    animation: preloaderSVGRotate 1s linear infinite;
  }
  
  @keyframes preloaderSVGRotate {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  